/* header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 30px 15px;

  @include pad {
    margin-left: -15px;
    margin-right: -15px;
  }

  @include mobile {
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 0;
    padding-bottom: 0;
  }
}

.logo {
  @include mobile {
    padding: 10px 164px;
  }

  a {
    display: block;

    background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/logo.png?raw=true);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    text-indent: 101%;
    white-space: nowrap;
    overflow: hidden;

    width: 80px;
    height: 48px;

    @include mobile {
      width: 48px;
      height: 29px;
    }
  }
}

.menu {
  display: flex;
  justify-content: space-between;

  @include mobile {
    flex-wrap: wrap;

    border: 1px solid $white;
    border-bottom: 0;
  }

  li {
    @include mobile {
      width: 50%;
      border: 1px solid $white;
    }
  }

  a {
    display: block;

    font-size: 1.25rem;

    &:hover {
      text-shadow: rgba(255, 255, 255, 0.1) -1.5px -1.5px 1px,
        rgba(0, 0, 0, 0.75) 1px 1px 1px;
      opacity: 1;
    }

    @include mobile {
      text-align: center;
      padding: 8px 54px;
    }
  }
}
