.blog{
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__menu{
        display: flex;
    }
    &__catagories{
        a{
            display: block;
            padding: 8px 24px;
         &:hover{
             background-color: $primary;
             border-radius: 5px;
             opacity: 1;
         }
        }
    }
}

.search{
    a{
        display: block;
        padding-top: 12px;
        padding-bottom: 12px;
    }
    img{
        width: 29px;
        height: 29px;

    }
}


/* 文章cards */

.blog{
    &__list{
        margin-bottom: 96px;

        @include pad{
            margin-bottom: 64px;
        }

        @include mobile{
            margin-bottom: 40px;
        }
    }

    &__body{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        margin-bottom: 60px;

        @include pad{
            margin-bottom: 24px;
        }
        
        @include mobile{
            flex-direction: column;
            margin-bottom: 20px;
        }
    }

    &__img{
        max-width: 57%;
        height: auto;
        object-fit: contain;

        @include pad{
            max-width: 48%;
            height: auto;
            object-fit: cover;
        }

        @include mobile{
            max-width: 100%;
        }
    }

    &__content{
        max-width: 40%;

        @include pad{
            max-width: 46%;
        }

        @include mobile{
            max-width: 100%;
        }
        h3{
            font-size: 2rem;
            line-height: 1.9;
            @include pad{
                font-size: 1.25rem;
            }
            @include mobile{
                font-size: 1rem;
            }

        }
    }

    &__subtitle{
        display: flex;
        justify-content: space-between;
        margin-bottom: 1.5rem;

        @include pad{
            margin-bottom: 2rem;
        }

        p{
            font-size: 1.25rem;
            @include pad{
                font-size: 1rem;
                line-height: 30px;
            }
            @include mobile{
                font-size: 0.875rem;
                
            }
        }
        span{
            font-size: 1rem;
            opacity: 0.7;
            @include pad{
                font-size: 0.875rem;
                line-height: 30px;
            }
        }
    }
    &__article{

        font-size: 1.25em;

        margin-bottom: 0.875rem;

        @include pad{
            font-size: 0.875rem;
            margin-bottom: 0;
        }

        @include mobile{
            display: none;
        }
    }

    &__link__lg{
        display: block;
        font-size: 2.25em;
        text-align: right;
        @include pad{
            display: none;
        }
       
    }

    &__link__md{
        display: none;
        @include pad{
            display: inline;

            font-size: 0.875em;


        }
        @include mobile{
            display: none;
        }

    }
}

